@use '../inc/vars' as *;

.management {
  background-color: $text-light;
  .container {

  }
  .section {
    &__header {
  
    }
    &__title {

    }
    &__header-additional {

    }
    &__subtitle {

    }
    &__body {
      
    }
  }
  &__accordion {
    display: flex;
    justify-content: space-between;
    height: auto !important;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      
    }
    @media(max-width: $sm){
      flex-direction: column;
      gap: 40px;
      padding-left: 30px;
      padding-right: 30px;
    }
    @media(max-width: $xs){
    
    }
    
  }
  &__el {
    width: calc(100%/3 - 20px);
    background-color: $green;
    border-radius: 75px;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
      border-radius: 40px;
    }
    @media(max-width: $md){
      
    }
    @media(max-width: $sm){
      width: 100%;
    }
    @media(max-width: $xs){
    
    }
    
    .circle-arrow {
      transform: rotate(90deg);
      margin-left: auto;
      margin-right: auto;
      margin-top: -70px;
      @media(max-width: $xl){
      
      }
      @media(max-width: $lg){
        margin-top: -55px;
      }
      @media(max-width: $md){
        
      }
      @media(max-width: $sm){
      
      }
      @media(max-width: $xs){
      
      }
      
      &:hover {
        background: $green;
      }
    }
    &:hover {
      .circle-arrow {
        background-color: $green;
        svg {
          * {
            stroke: $text;
          }
        }
      }
    }
    &.open {
      .circle-arrow {
        transform: rotate(-90deg);
      }
    }
  }
  &__el-header {
    padding-bottom: 40px;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
      padding-bottom: 25px;
    }
    @media(max-width: $md){
      
    }
    @media(max-width: $sm){
    
    }
    @media(max-width: $xs){
    
    }
    
  }
  &__el-img {
    border-radius: 75px;
    width: 100%;
    height: 336px;
    object-fit: cover;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
      border-radius: 40px;
    }
    @media(max-width: $md){
      
      height: 200px;
    }
    @media(max-width: $sm){
      
    }
    @media(max-width: $xs){
    
    }
    
  }
  &__el-title {
    @extend .h2 !optional;
    padding-left: 50px;
    padding-right: 50px;
    margin-top: 10px;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
      padding-left: 30px;
      padding-right: 30px;
      font-size: 28px;
    }
    @media(max-width: $md){
      
    }
    @media(max-width: $sm){
    
    }
    @media(max-width: $xs){
    
    }
    
  }
  &__el-content {
    padding-left: 50px;
    padding-right: 50px;
    padding-bottom: 50px;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
      padding-left: 30px;
      padding-right: 30px;
      padding-bottom: 30px;
    }
    @media(max-width: $md){
      
    }
    @media(max-width: $sm){
    
    }
    @media(max-width: $xs){
    
    }
    
  }



  .decor {
    background-image: url(../img/svg/decor-management.svg);
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      right: -100px !important;
    }
    @media(max-width: $sm){
    
    }
    @media(max-width: $xs){
      background-position: right bottom;
      right: -200px !important;
      left: auto;
    }
    
    
  }
}

